	module.exports = {"data":{"postsJson":{"title":"Вызов, Исцеления и Обучения","questionsTitle":"Заповеди и Обещания","display":"block","bookName":"От Луки ","ref":"5:1-6:49","html":"<p>   <strong>5</strong><u>1</u>Однажды, когда народ теснился к Нему, чтобы слышать слово Божие, а Он стоял у озера Геннисаретского,<br />   <u>2</u>увидел Он две лодки, стоящие на озере; а рыболовы, выйдя из них, вымывали сети.<br />   <u>3</u>Войдя в одну лодку, которая была Симонова, Он просил его отплыть несколько от берега и, сев, учил народ из лодки.<br />   <u>4</u>Когда же перестал учить, сказал Симону: </p><h1>JESUS</h1><h2>отплыви на глубину и закиньте сети свои для лова.</h2><p> <u>5</u>Симон сказал Ему в ответ: </p><h1>SIMON PETER</h1><h3>Наставник! мы трудились всю ночь и ничего не поймали, но по слову Твоему закину сеть.</h3><p> <u>6</u>Сделав это, они поймали великое множество рыбы, и даже сеть у них прорывалась.<br />   <u>7</u>И дали знак товарищам, находившимся на другой лодке, чтобы пришли помочь им; и пришли, и наполнили обе лодки, так что они начинали тонуть.<br />   <u>8</u>Увидев это, Симон Петр припал к коленям Иисуса и сказал: </p><h1>SIMON PETER</h1><h3>выйди от меня, Господи! потому что я человек грешный.</h3><p> <u>9</u>Ибо ужас объял его и всех, бывших с ним, от этого лова рыб, ими пойманных;<br />   <u>10</u>также и Иакова и Иоанна, сыновей Зеведеевых, бывших товарищами Симону. И сказал Симону Иисус: </p><h1>JESUS</h1><h2>не бойся; отныне будешь ловить человеков.</h2><p> <u>11</u>И, вытащив обе лодки на берег, оставили всё и последовали за Ним.<br />   <u>12</u>Когда Иисус был в одном городе, пришел человек весь в проказе и, увидев Иисуса, пал ниц, умоляя Его и говоря: </p><h1>A LEPER</h1><h4>Господи! если хочешь, можешь меня очистить.</h4><p> <u>13</u>Он простер руку, прикоснулся к нему и сказал: </p><h1>JESUS</h1><h2>хочу, очистись. </h2><p>И тотчас проказа сошла с него.<br />   <u>14</u>И Он повелел ему никому не сказывать, </p><h1>JESUS</h1><h2>а пойти показаться священнику и принести [жертву] за очищение свое, как повелел Моисей, во свидетельство им.</h2><p> <u>15</u>Но тем более распространялась молва о Нём, и великое множество народа стекалось к Нему слушать и врачеваться у Него от болезней своих.<br />   <u>16</u>Но Он уходил в пустынные места и молился.<br />   <u>17</u>В один день, когда Он учил, и сидели тут фарисеи и законоучители, пришедшие из всех мест Галилеи и Иудеи и из Иерусалима, и сила Господня являлась в исцелении [больных], --<br />   <u>18</u>вот, принесли некоторые на постели человека, который был расслаблен, и старались внести его [в дом] и положить перед Иисусом;<br />   <u>19</u>и, не найдя, где пронести его за многолюдством, влезли на верх дома и сквозь кровлю спустили его с постелью на средину пред Иисуса.<br />   <u>20</u>И Он, видя веру их, сказал человеку тому: </p><h1>JESUS</h1><h2>прощаются тебе грехи твои.</h2><p> <u>21</u>Книжники и фарисеи начали рассуждать, говоря: </p><h1>THE PHARISEES &amp; THE RELIGIOUS LEADERS</h1><p>кто это, который богохульствует? кто может прощать грехи, кроме одного Бога?<br />   <u>22</u>Иисус, уразумев помышления их, сказал им в ответ: что вы помышляете в сердцах ваших?<br />   <u>23</u>Что легче сказать: </p><h1>JESUS</h1><h2>прощаются тебе грехи твои, или сказать: встань и ходи?<br />   <u>24</u>Но чтобы вы знали, что Сын Человеческий имеет власть на земле прощать грехи, </h2><p>--сказал Он расслабленному: </p><h1>JESUS</h1><h2>тебе говорю: встань, возьми постель твою и иди в дом твой.</h2><p> <u>25</u>И он тотчас встал перед ними, взял, на чём лежал, и пошел в дом свой, славя Бога.<br />   <u>26</u>И ужас объял всех, и славили Бога и, быв исполнены страха, говорили: </p><h1>PEOPLE IN CAPERNAUM</h1><h4>чудные дела видели мы ныне.</h4><p> <u>27</u>После сего [Иисус] вышел и увидел мытаря, именем Левия, сидящего у сбора пошлин, и говорит ему: </p><h1>JESUS</h1><h2>следуй за Мною.</h2><p> <u>28</u>И он, оставив всё, встал и последовал за Ним.<br />   <u>29</u>И сделал для Него Левий в доме своем большое угощение; и там было множество мытарей и других, которые возлежали с ними.<br />   <u>30</u>Книжники же и фарисеи роптали и говорили ученикам Его: </p><h1>THE PHARISEES &amp; THE RELIGIOUS LEADERS</h1><h4>зачем вы едите и пьете с мытарями и грешниками?</h4><p> <u>31</u>Иисус же сказал им в ответ: </p><h1>JESUS</h1><h2>не здоровые имеют нужду во враче, но больные;<br />   <u>32</u>Я пришел призвать не праведников, а грешников к покаянию.</h2><p> <u>33</u>Они же сказали Ему: </p><h1>SOME PEOPLE</h1><h4>почему ученики Иоанновы постятся часто и молитвы творят, также и фарисейские, а Твои едят и пьют?</h4><p> <u>34</u>Он сказал им: </p><h1>JESUS</h1><h2>можете ли заставить сынов чертога брачного поститься, когда с ними жених?<br />   <u>35</u>Но придут дни, когда отнимется у них жених, и тогда будут поститься в те дни.</h2><p> <u>36</u>При сем сказал им притчу: </p><h1>JESUS</h1><h2>никто не приставляет заплаты к ветхой одежде, отодрав от новой одежды; а иначе и новую раздерет, и к старой не подойдет заплата от новой.<br />   <u>37</u>И никто не вливает молодого вина в мехи ветхие; а иначе молодое вино прорвет мехи, и само вытечет, и мехи пропадут;<br />   <u>38</u>но молодое вино должно вливать в мехи новые; тогда сбережется и то и другое.<br />   <u>39</u>И никто, пив старое [вино], не захочет тотчас молодого, ибо говорит: старое лучше.</h2><p>   <strong>6</strong><u>1</u>В субботу, первую по втором дне Пасхи, случилось Ему проходить засеянными полями, и ученики Его срывали колосья и ели, растирая руками.<br />   <u>2</u>Некоторые же из фарисеев сказали им: </p><h1>THE PHARISEES</h1><h4>зачем вы делаете то, чего не должно делать в субботы?</h4><p> <u>3</u>Иисус сказал им в ответ: </p><h1>JESUS</h1><h2>разве вы не читали, что сделал Давид, когда взалкал сам и бывшие с ним?<br />   <u>4</u>Как он вошел в дом Божий, взял хлебы предложения, которых не должно было есть никому, кроме одних священников, и ел, и дал бывшим с ним?</h2><p> <u>5</u>И сказал им: </p><h1>JESUS</h1><h2>Сын Человеческий есть господин и субботы.</h2><p> <u>6</u>Случилось же и в другую субботу войти Ему в синагогу и учить. Там был человек, у которого правая рука была сухая.<br />   <u>7</u>Книжники же и фарисеи наблюдали за Ним, не исцелит ли в субботу, чтобы найти обвинение против Него.<br />   <u>8</u>Но Он, зная помышления их, сказал человеку, имеющему сухую руку: </p><h1>JESUS</h1><h2>встань и выступи на средину. </h2><p>И он встал и выступил.<br />   <u>9</u>Тогда сказал им Иисус: </p><h1>JESUS</h1><h2>спрошу Я вас: что должно делать в субботу? добро, или зло? спасти душу, или погубить? Они молчали.</h2><p> <u>10</u>И, посмотрев на всех их, сказал тому человеку: </p><h1>JESUS</h1><h2>протяни руку твою. </h2><p>Он так и сделал; и стала рука его здорова, как другая.<br />   <u>11</u>Они же пришли в бешенство и говорили между собою, что бы им сделать с Иисусом.<br />   <u>12</u>В те дни взошел Он на гору помолиться и пробыл всю ночь в молитве к Богу.<br />   <u>13</u>Когда же настал день, призвал учеников Своих и избрал из них двенадцать, которых и наименовал Апостолами:<br />   <u>14</u>Симона, которого и назвал Петром, и Андрея, брата его, Иакова и Иоанна, Филиппа и Варфоломея,<br />   <u>15</u>Матфея и Фому, Иакова Алфеева и Симона, прозываемого Зилотом,<br />   <u>16</u>Иуду Иаковлева и Иуду Искариота, который потом сделался предателем.<br />   <u>17</u>И, сойдя с ними, стал Он на ровном месте, и множество учеников Его, и много народа из всей Иудеи и Иерусалима и приморских мест Тирских и Сидонских,<br />   <u>18</u>которые пришли послушать Его и исцелиться от болезней своих, также и страждущие от нечистых духов; и исцелялись.<br />   <u>19</u>И весь народ искал прикасаться к Нему, потому что от Него исходила сила и исцеляла всех.<br />   <u>20</u>И Он, возведя очи Свои на учеников Своих, говорил: </p><h1>JESUS</h1><h2>Блаженны нищие духом, ибо ваше есть Царствие Божие.<br />   <u>21</u>Блаженны алчущие ныне, ибо насытитесь. Блаженны плачущие ныне, ибо воссмеетесь.<br />   <u>22</u>Блаженны вы, когда возненавидят вас люди и когда отлучат вас, и будут поносить, и пронесут имя ваше, как бесчестное, за Сына Человеческого.<br />   <u>23</u>Возрадуйтесь в тот день и возвеселитесь, ибо велика вам награда на небесах. Так поступали с пророками отцы их.<br />   <u>24</u>Напротив, горе вам, богатые! ибо вы уже получили свое утешение.<br />   <u>25</u>Горе вам, пресыщенные ныне! ибо взалчете. Горе вам, смеющиеся ныне! ибо восплачете и возрыдаете.<br />   <u>26</u>Горе вам, когда все люди будут говорить о вас хорошо! ибо так поступали с лжепророками отцы их.<br />   <u>27</u>Но вам, слушающим, говорю: любите врагов ваших, благотворите ненавидящим вас,<br />   <u>28</u>благословляйте проклинающих вас и молитесь за обижающих вас.<br />   <u>29</u>Ударившему тебя по щеке подставь и другую, и отнимающему у тебя верхнюю одежду не препятствуй взять и рубашку.<br />   <u>30</u>Всякому, просящему у тебя, давай, и от взявшего твое не требуй назад.<br />   <u>31</u>И как хотите, чтобы с вами поступали люди, так и вы поступайте с ними.<br />   <u>32</u>И если любите любящих вас, какая вам за то благодарность? ибо и грешники любящих их любят.<br />   <u>33</u>И если делаете добро тем, которые вам делают добро, какая вам за то благодарность? ибо и грешники то же делают.<br />   <u>34</u>И если взаймы даёте тем, от которых надеетесь получить обратно, какая вам за то благодарность? ибо и грешники дают взаймы грешникам, чтобы получить обратно столько же.<br />   <u>35</u>Но вы любите врагов ваших, и благотворите, и взаймы давайте, не ожидая ничего; и будет вам награда великая, и будете сынами Всевышнего; ибо Он благ и к неблагодарным и злым.<br />   <u>36</u>Итак, будьте милосерды, как и Отец ваш милосерд.<br />   <u>37</u>Не судите, и не будете судимы; не осуждайте, и не будете осуждены; прощайте, и прощены будете;<br />   <u>38</u>давайте, и дастся вам: мерою доброю, утрясенною, нагнетенною и переполненною отсыплют вам в лоно ваше; ибо, какою мерою мерите, такою же отмерится и вам.</h2><p> <u>39</u>Сказал также им притчу: </p><h1>JESUS</h1><h2>может ли слепой водить слепого? не оба ли упадут в яму?<br />   <u>40</u>Ученик не бывает выше своего учителя; но, и усовершенствовавшись, будет всякий, как учитель его.<br />   <u>41</u>Что ты смотришь на сучок в глазе брата твоего, а бревна в твоем глазе не чувствуешь?<br />   <u>42</u>Или, как можешь сказать брату твоему: брат! дай, я выну сучок из глаза твоего, когда сам не видишь бревна в твоем глазе? Лицемер! вынь прежде бревно из твоего глаза, и тогда увидишь, как вынуть сучок из глаза брата твоего.<br />   <u>43</u>Нет доброго дерева, которое приносило бы худой плод; и нет худого дерева, которое приносило бы плод добрый,<br />   <u>44</u>ибо всякое дерево познаётся по плоду своему, потому что не собирают смокв с терновника и не снимают винограда с кустарника.<br />   <u>45</u>Добрый человек из доброго сокровища сердца своего выносит доброе, а злой человек из злого сокровища сердца своего выносит злое, ибо от избытка сердца говорят уста его.<br />   <u>46</u>Что вы зовете Меня: Господи! Господи! --и не делаете того, что Я говорю?<br />   <u>47</u>Всякий, приходящий ко Мне и слушающий слова Мои и исполняющий их, скажу вам, кому подобен.<br />   <u>48</u>Он подобен человеку, строящему дом, который копал, углубился и положил основание на камне; почему, когда случилось наводнение и вода напёрла на этот дом, то не могла поколебать его, потому что он основан был на камне.<br />   <u>49</u>А слушающий и неисполняющий подобен человеку, построившему дом на земле без основания, который, когда напёрла на него вода, тотчас обрушился; и разрушение дома сего было великое.</h2>","path":"/328","Question1":"Какие Богом данные указания вы замечаете в этой части Писания?","Question2":"Поставьте себя в исходное положение и творчески подумайте о том, как бы это выглядела подучить эту инструкцию? Какое отношение это имеет к вам сегодня, где вы работайте и живёте?","Question3":"Какую божественную надежду вы замечаете в этой части Писания? Как вы можете расти, чтобы иметь больше этого в своей собственной жизни?","Question4":"Посвяти себя тому чтобы рассказать другу о том, что сегодня означает эта надежда для вас.  ","prevPath":"/327","nextPath":"/329"}},"pathContext":{}}

